
import { Component, Prop, Provide, Vue } from 'vue-property-decorator'

@Component
export default class Dialog extends Vue {
    visible = {
        val: false
    }
    @Provide('visible')
    visibleVal = this.visible
    // props
    @Prop({ default: '' }) title!: string //
    @Prop({ default: '' }) content!: string //
    @Prop({ default: '' }) confirmButtonText!: string //
    @Prop({ default: '' }) cancelButtonText!: string | boolean //
    @Prop({ default: '400px' }) width!: string | number //
    @Prop({ default: '40vh' }) top!: string | number //
    @Prop({ default: false }) disabled!: boolean //
    @Prop({ default: false }) async!: boolean //
    @Prop({ default: true }) clickModalClose!: boolean // 
    // data
    
    // methods
    handleEvent(type: 'cancel' | 'confirm') {
        this.$emit(type)
        if (!this.async || type === 'cancel') this.close()
    }

    onTrigger() {
        if (this.disabled) return
        this.open()
    }

    close() {
        this.visible.val = false
        this.$emit('close')
    }
    open() {
        this.visible.val = true
        this.$emit('open')
    }
}
